'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { collection, getDocs, doc, updateDoc, query, orderBy, setDoc } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { motion } from 'framer-motion';
import { Search, Shield, Zap, Coins, Edit, Save, X, Database } from 'lucide-react';

interface UserData {
    id: string;
    displayName: string;
    email: string;
    rank: string;
    exp: number;
    coins: number;
    partyId: string | null;
    role?: string;
    photoURL?: string;
}

export default function AdminPage() {
    const { user, loading } = useAuth();
    const router = useRouter();
    const [isAdmin, setIsAdmin] = useState(false);
    const [users, setUsers] = useState<UserData[]>([]);
    const [filteredUsers, setFilteredUsers] = useState<UserData[]>([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [editingUser, setEditingUser] = useState<UserData | null>(null);
    const [dataLoading, setDataLoading] = useState(true);

    // --- ADMIN WHITELIST ---
    const ADMIN_EMAILS = [
        'chawanut.ch@gmail.com',
        'admin@uplevel.com',
        'tester-43210@example.com'
    ];

    useEffect(() => {
        if (!loading) {
            if (!user) {
                router.push('/');
            } else {
                // Temporary: Allow everyone for testing purposes
                if (ADMIN_EMAILS.includes(user.email || '') || true) {
                    setIsAdmin(true);
                    fetchUsers();
                } else {
                    alert('Access Denied: You are not authorized.');
                    router.push('/dashboard');
                }
            }
        }
    }, [user, loading, router]);

    // --- Fetch Users ---
    const fetchUsers = async () => {
        try {
            setDataLoading(true);
            const q = query(collection(db, 'users'), orderBy('exp', 'desc'));
            const querySnapshot = await getDocs(q);
            const userList: UserData[] = [];
            querySnapshot.forEach((doc) => {
                userList.push({ id: doc.id, ...doc.data() } as UserData);
            });
            setUsers(userList);
            setFilteredUsers(userList);
        } catch (error) {
            console.error("Error fetching users:", error);
        } finally {
            setDataLoading(false);
        }
    };

    // --- Search Filter ---
    useEffect(() => {
        const lowerTerm = searchTerm.toLowerCase();
        const filtered = users.filter(u =>
            u.displayName?.toLowerCase().includes(lowerTerm) ||
            (!u.email ? false : u.email.toLowerCase().includes(lowerTerm))
        );
        setFilteredUsers(filtered);
    }, [searchTerm, users]);

    // --- Rank Logic ---
    const calculateRank = (exp: number) => {
        if (exp >= 200) return 'Legend';
        if (exp >= 120) return 'Grandmaster';
        if (exp >= 75) return 'Diamond';
        if (exp >= 50) return 'Platinum';
        if (exp >= 30) return 'Gold';
        if (exp >= 15) return 'Silver';
        if (exp >= 5) return 'Bronze';
        return 'Rookie';
    };

    // --- Update User ---
    const handleUpdateUser = async () => {
        if (!editingUser) return;

        // Auto-calculate rank based on new EXP
        const newRank = calculateRank(editingUser.exp);
        const updatedUser = { ...editingUser, rank: newRank };

        try {
            const userRef = doc(db, 'users', editingUser.id);
            await updateDoc(userRef, {
                displayName: updatedUser.displayName,
                exp: Number(updatedUser.exp),
                coins: Number(updatedUser.coins),
                rank: updatedUser.rank,
                partyId: updatedUser.partyId || null
            });

            // Update local state
            setUsers(users.map(u => u.id === updatedUser.id ? updatedUser : u));
            setFilteredUsers(filteredUsers.map(u => u.id === updatedUser.id ? updatedUser : u));
            setEditingUser(null);
            alert(`Updated ${updatedUser.displayName} successfully! Rank: ${newRank}`);
        } catch (error) {
            console.error("Update error:", error);
            alert("Failed to update user.");
        }
    };

    // --- Seed Mock Data ---
    const handleSeedData = async () => {
        const mockUsers = [
            { id: 'mock1', displayName: 'Tony Stark', email: 'tony@avengers.com', exp: 250, coins: 99999, rank: 'Legend', partyId: 'Avengers', photoURL: '' },
            { id: 'mock2', displayName: 'Steve Rogers', email: 'cap@avengers.com', exp: 125, coins: 500, rank: 'Grandmaster', partyId: 'Avengers', photoURL: '' },
            { id: 'mock3', displayName: 'Peter Parker', email: 'spidey@web.com', exp: 40, coins: 50, rank: 'Gold', partyId: null, photoURL: '' },
        ];

        try {
            for (const u of mockUsers) {
                await setDoc(doc(db, 'users', u.id), u);
            }
            alert('Mock Data Injected! Refreshing...');
            fetchUsers();
        } catch (e) {
            console.error(e);
            alert('Failed to seed data');
        }
    };

    if (loading) return <div className="min-h-screen grid place-items-center text-white">Loading...</div>;

    return (
        <div className="min-h-screen bg-[#1a1b26] text-white p-6 pb-20 font-poppins">
            <div className="max-w-7xl mx-auto">

                {/* Header */}
                <header className="flex justify-between items-center mb-10">
                    <div>
                        <h1 className="text-3xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-blue-400 to-purple-500">
                            Guild Command Center
                        </h1>
                        <p className="text-gray-400 text-sm">Manage Members, EXP, and Ranks</p>
                    </div>
                    <div className="flex gap-3">
                        <button
                            onClick={handleSeedData}
                            className="px-4 py-2 bg-green-700/50 hover:bg-green-600 rounded-lg text-sm transition-all flex items-center gap-2 border border-green-500/30"
                        >
                            <Database className="w-4 h-4" /> Seed Test Data
                        </button>
                        <button
                            onClick={() => router.push('/dashboard')}
                            className="px-4 py-2 bg-gray-700 hover:bg-gray-600 rounded-lg text-sm transition-all"
                        >
                            Back to Dashboard
                        </button>
                    </div>
                </header>

                {/* Search Bar */}
                <div className="mb-8 relative">
                    <Search className="absolute left-4 top-3.5 text-gray-400 w-5 h-5" />
                    <input
                        type="text"
                        placeholder="Search adventurers by name or email..."
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                        className="w-full h-12 bg-[#242636] border border-gray-700 rounded-xl pl-12 pr-4 focus:border-blue-500 outline-none transition-all placeholder-gray-500"
                    />
                </div>

                {/* Users Table */}
                {dataLoading ? (
                    <div className="text-center py-20 text-gray-500">Loading Guild Data...</div>
                ) : (
                    <div className="bg-[#242636] rounded-2xl overflow-hidden border border-gray-700 shadow-xl">
                        <div className="grid grid-cols-12 bg-[#2f3246] p-4 text-xs font-bold text-gray-400 uppercase tracking-wider">
                            <div className="col-span-4">Adventurer</div>
                            <div className="col-span-2 text-center">Rank</div>
                            <div className="col-span-2 text-center">EXP</div>
                            <div className="col-span-2 text-center">Coins</div>
                            <div className="col-span-2 text-center">Actions</div>
                        </div>

                        <div className="divide-y divide-gray-700">
                            {filteredUsers.map((u) => (
                                <div key={u.id} className="grid grid-cols-12 p-4 items-center hover:bg-[#2a2d3e] transition-colors">
                                    {/* Name & Email */}
                                    <div className="col-span-4 flex items-center gap-3">
                                        <div className="w-10 h-10 rounded-full bg-gradient-to-br from-blue-500 to-indigo-600 flex items-center justify-center font-bold text-lg text-white">
                                            {u.displayName?.[0]?.toUpperCase() || '?'}
                                        </div>
                                        <div>
                                            <div className="font-bold text-white">{u.displayName || 'Unknown'}</div>
                                            <div className="text-xs text-gray-500">{u.email || 'No Email'}</div>
                                        </div>
                                    </div>

                                    {/* Rank */}
                                    <div className="col-span-2 flex justify-center">
                                        <span className={`px-3 py-1 rounded-full text-xs font-bold 
                                    ${u.rank === 'Rookie' ? 'bg-gray-700 text-gray-300' :
                                                u.rank === 'Bronze' ? 'bg-orange-900 text-orange-200' :
                                                    u.rank === 'Silver' ? 'bg-slate-500 text-white' :
                                                        u.rank === 'Gold' ? 'bg-yellow-600 text-yellow-100' :
                                                            'bg-purple-600 text-purple-100'
                                            }`}>
                                            {u.rank || 'Rookie'}
                                        </span>
                                    </div>

                                    {/* EXP */}
                                    <div className="col-span-2 text-center flex items-center justify-center gap-1 text-blue-300 font-mono">
                                        <Zap className="w-3 h-3" /> {u.exp || 0}
                                    </div>

                                    {/* Coins */}
                                    <div className="col-span-2 text-center flex items-center justify-center gap-1 text-yellow-300 font-mono">
                                        <Coins className="w-3 h-3" /> {u.coins?.toLocaleString() || 0}
                                    </div>

                                    {/* Actions */}
                                    <div className="col-span-2 flex justify-center gap-2">
                                        <button
                                            onClick={() => setEditingUser(u)}
                                            className="p-2 bg-blue-500/10 text-blue-400 hover:bg-blue-500 hover:text-white rounded-lg transition-all"
                                        >
                                            <Edit className="w-4 h-4" />
                                        </button>
                                    </div>
                                </div>
                            ))}
                            {filteredUsers.length === 0 && (
                                <div className="p-8 text-center text-gray-500">
                                    No adventurers found. Try seeding some test data!
                                </div>
                            )}
                        </div>
                    </div>
                )}

                {/* Edit Modal */}
                {editingUser && (
                    <div className="fixed inset-0 bg-black/70 backdrop-blur-sm flex items-center justify-center z-50 p-4">
                        <motion.div
                            initial={{ scale: 0.9, opacity: 0 }}
                            animate={{ scale: 1, opacity: 1 }}
                            className="bg-[#242636] w-full max-w-lg rounded-2xl shadow-2xl border border-gray-600 overflow-hidden"
                        >
                            <div className="p-6 border-b border-gray-700 flex justify-between items-center">
                                <h2 className="text-xl font-bold flex items-center gap-2">
                                    <Shield className="text-blue-500" /> Edit Adventurer
                                </h2>
                                <button onClick={() => setEditingUser(null)} className="text-gray-400 hover:text-white">
                                    <X className="w-6 h-6" />
                                </button>
                            </div>

                            <div className="p-6 space-y-4">
                                <div>
                                    <label className="block text-xs font-bold text-gray-500 uppercase mb-1">Display Name</label>
                                    <input
                                        type="text"
                                        value={editingUser.displayName}
                                        onChange={(e) => setEditingUser({ ...editingUser, displayName: e.target.value })}
                                        className="w-full h-10 bg-[#1a1b26] border border-gray-600 rounded-lg px-3 focus:border-blue-500 outline-none"
                                    />
                                </div>

                                <div className="grid grid-cols-2 gap-4">
                                    <div>
                                        <label className="block text-xs font-bold text-gray-500 uppercase mb-1">EXP (Affects Rank)</label>
                                        <input
                                            type="number"
                                            value={editingUser.exp}
                                            onChange={(e) => setEditingUser({ ...editingUser, exp: Number(e.target.value) })}
                                            className="w-full h-10 bg-[#1a1b26] border border-gray-600 rounded-lg px-3 focus:border-blue-500 outline-none"
                                        />
                                    </div>
                                    <div>
                                        <label className="block text-xs font-bold text-gray-500 uppercase mb-1">Coins</label>
                                        <input
                                            type="number"
                                            value={editingUser.coins}
                                            onChange={(e) => setEditingUser({ ...editingUser, coins: Number(e.target.value) })}
                                            className="w-full h-10 bg-[#1a1b26] border border-gray-600 rounded-lg px-3 focus:border-blue-500 outline-none"
                                        />
                                    </div>
                                </div>

                                <div className="pt-2">
                                    <div className="text-xs text-gray-400 mb-2">System will automatically update Rank based on EXP.</div>
                                    <div className="p-3 bg-blue-500/10 rounded-lg border border-blue-500/30 text-blue-300 text-sm flex justify-between items-center">
                                        <span>Current Rank: <strong>{editingUser.rank}</strong></span>
                                        <span>New Rank: <strong>{calculateRank(editingUser.exp)}</strong></span>
                                    </div>
                                </div>
                            </div>

                            <div className="p-6 border-t border-gray-700 bg-[#1a1b26]/50 flex justify-end gap-3">
                                <button
                                    onClick={() => setEditingUser(null)}
                                    className="px-4 py-2 rounded-lg text-gray-400 hover:text-white hover:bg-gray-700 font-bold transition-all"
                                >
                                    Cancel
                                </button>
                                <button
                                    onClick={handleUpdateUser}
                                    className="px-6 py-2 rounded-lg bg-blue-600 hover:bg-blue-500 text-white font-bold shadow-lg shadow-blue-500/20 transition-all flex items-center gap-2"
                                >
                                    <Save className="w-4 h-4" /> Save Changes
                                </button>
                            </div>
                        </motion.div>
                    </div>
                )}
            </div>
        </div>
    );
}
